<template>
    <div>
        <Header>小区周边</Header>
       <div id="container" style="width:100%; height:100%;resize:both;"></div>
    </div>
</template>
<script>
import Header from '../../components/Header'
import AMap from 'AMap'
export default {
  components: {Header},
  mounted () {
      var map;
  map = new AMap.Map('container', {
    resizeEnable: true,
    rotateEnable:true,
    pitchEnable:true,
    zoom: 17,
    pitch:80,
    rotation:-15,
    viewMode:'3D',//开启3D视图,默认为关闭
    buildingAnimation:true,//楼块出现是否带动画
    
    expandZoomRange:true,
    zooms:[3,20],
    center:[106.509879,29.598461]
  });
  
  map.addControl(new AMap.ControlBar({
    showZoomBar:false,
    showControlButton:true,
    position:{
      right:'10px',
      top:'10px'
    }
  }))
  }
}
</script>
<style scoped>
.map-container{
    padding-top: 40px;
}
.container{
  height: 100%
}
.container{
  height: 100%
}
</style>


